<template>
	<view>
		<w-sselect :defaultValue="defaultValue" :bgColor="bgColor" :value="value" :width="width" @click='openPicker'></w-sselect>
		<u-picker @confirm="confirm" @cancel="close" :show="show" :columns="dataList" :keyName="keyName">
		</u-picker>
	</view>
</template>
<script>
	export default {
		name: "wPicker",
		props: {
			//默认显示的文字
			defaultValue: {
				type: String,
				default: ''
			},
			bgColor:{
				type: String,
				default: '#eee'
			},
			//绑定的值
			value: {
				default: ''
			},
			//设置宽度
			width: {
				default: '550rpx'
			},
			//数组列表
			list: {
				default: () => []
			},
			//数组绑定的key值，即需要显示的值
			keyName: {
				default: ''
			}
		},
		computed: {
			dataList() {
				const List = []
				List.push(this.list)
				return List
			}
		},
		data() {
			return {
				show: false
			}
		},
		methods: {
			openPicker() {
				this.show=true
			},
			confirm(e) {
				this.$emit('confirm', e)
				this.show=false
			},
			close() {
				this.$emit('close')
				this.show=false
			}
		},
	}
</script>
<style lang="scss" scoped>
</style>
